import React from "react";

function ThemeChanger(props) {
  return (
    <div>
      <div className="theme-changer">
        <div onClick={props.themeHanlder} className="theme-changer-input">
          <div
            className="theme-changer-after"
            style={
              props.state.theme === "light"
                ? {
                    right: 2 + "px",
                  }
                : { left: 2 + "px" }
            }
          ></div>
        </div>
        {props.state.theme === "light" ? "Dark Theme" : "Light Theme"}
      </div>
    </div>
  );
}

export default ThemeChanger;
