export const mathReducer = (state, action) => {
  switch (action.type) {
    case "ADD":
      if (action.value === "." && state.result.includes(".")) {
        return state;
      }
      return {
        ...state,
        result: state.result + action.value,
      };

    case "CLEAR_ALL":
      return {
        result: "",
      };

    case "DELETE":
      return {
        ...state,
        result: state.result.slice(0, -1),
      };

    case "NEGATIVE":
      return {
        ...state,
        result: "-" + state.result,
      };

    case "PERCENT":
      return {
        ...state,
        result: String(eval(state.result) / 100),
      };

    case "EQUAL":
      console.log(state.result, typeof state.result);
      return {
        ...state,
        history: state.result,
        result: String(eval(state.result)),
      };

    default:
      return state;
  }
};

export const appReducer = (state, action) => {
  switch (action.type) {
    case "THEME-DARK":
      return {
        ...state,
        theme: "dark",
      };
    case "THEME-LIGHT":
      return {
        ...state,
        theme: "light",
      };
    default:
      return state;
  }
};
