// IMPORTS
import React, { useReducer } from "react";
import Buttons from "../Buttons/Buttons";
import HistoryResult from "../History+Result/History+Result";
import { mathReducer, appReducer } from "../Reducer/Reducer";
import ThemeChanger from "../ThemeChanger/ThemeChanger";
import "./Home.css";

// STATES
const initialValues = {
    history: "",
    result: "",
  },
  initialState = {
    theme: "dark",
  },
  // HOMEPAGE
  Home = () => {
    // Reducer for Calculator Class
    const [ClassState, ClassDispatch] = useReducer(appReducer, initialState),
      classes = {
        dark: {
          color1: "black",
          color2: "#1E2225",
          color3: "#1E2225",
          color4: "#292C2F",
          color5: "#6A7677",
          color6: "#ffffff",
        },
        light: {
          color1: "white",
          color2: "#c1c1c1",
          color3: "#dcdcdc",
          color4: "#d0d3d4",
          color5: "#6A7677",
          color6: "#5D6770",
        },
      },
      themeHanlder = () => {
        ClassState.theme === "light"
          ? ClassDispatch({ type: "THEME-DARK" })
          : ClassDispatch({ type: "THEME-LIGHT" });
      },
      [ValueState, ValueDispatch] = useReducer(mathReducer, initialValues),
      // Click handler
      clickHandler = (e, operate) => {
        console.log(operate);
        switch (operate) {
          case "clear":
            ValueDispatch({ type: "CLEAR_ALL" });
            break;

          case "delete":
            ValueDispatch({ type: "DELETE", value: operate });
            break;

          case "1":
          case "2":
          case "3":
          case "4":
          case "5":
          case "6":
          case "7":
          case "8":
          case "9":
          case "0":
          case "(":
          case ")":
          case "+":
          case "-":
          case "*":
          case "/":
          case ".":
            ValueDispatch({ type: "ADD", value: operate });
            break;

          case "=":
            ValueDispatch({ type: "EQUAL" });
            break;
          case "negative":
            ValueDispatch({ type: "NEGATIVE" });
            break;
          case "percentage":
            ValueDispatch({ type: "PERCENT" });
            break;
          default:
            break;
        }
      };

    return (
      <React.Fragment>
        <div
          className="calculator"
          style={{
            background: `linear-gradient(135deg, ${
              classes[`${ClassState.theme}`]["color1"]
            },
        ${classes[`${ClassState.theme}`]["color2"]})`,
          }}
        >
          <div
            className="main-calculator"
            style={{
              background: `linear-gradient(180deg, ${
                classes[`${ClassState.theme}`]["color3"]
              }, ${classes[`${ClassState.theme}`]["color4"]})`,
            }}
          >
            <div className="upper">
              <HistoryResult
                classes={classes}
                state={ClassState}
                values={ValueState}
              />
              <ThemeChanger themeHanlder={themeHanlder} state={ClassState} />
            </div>
            <Buttons clickHandler={clickHandler} />
          </div>
        </div>
      </React.Fragment>
    );
  };

export default Home;
