import React from "react";

function HistoryResult(props) {
  return (
    <div>
      <div
        className="result"
        style={{
          color: `${props.classes[`${props.state.theme}`]["color6"]}`,
        }}
      >
        {props.values.result || 0}
      </div>
      <div
        className="history"
        style={{
          color: `${props.classes[`${props.state.theme}`]["color5"]}`,
        }}
      >
        {props.values.history}
      </div>
    </div>
  );
}

export default HistoryResult;
